/* list_view */
$list-view-border-color: #ededed;
$list-view-background: #ffffff;
$list-view-border-radius: 6px;
$list-view-input-color: #363636;

.list_view {
    .list-head {
	display: flex;
	justify-content: space-between;
	padding-bottom: 20px;
	align-items: center;
    }
    .list-head-right {
	display: flex;
	align-items: center;
	margin: -10px;
	>div {
	    margin: 0 10px;
	}
	>button {
	    margin: 0 10px;
	}
	.list-actions {
	    .button {
		margin: 0 10px;
                &:last-of-type {
		    margin-right: 0;
		}

	    }
	}
    }
    .list-body {
        background: $list-view-background;
        // border: 1px solid $list-view-border-color;
	border-bottom: none;
	border-radius: $list-view-border-radius;
	-webkit-border-radius: $list-view-border-radius;
        
        // -webkit-box-shadow: 0 0 20px 0 rgba(0,0,0,0.05);
        // box-shadow: 0 0 20px 0 rgba(0,0,0,0.05);
        box-shadow: rgba(136, 152, 170, 0.15) 0px 0px 32px 0px;
        -webkit-box-shadow: rgba(136, 152, 170, 0.15) 0px 0px 32px 0px;
        overflow: visible;
        &::after {
            content: "";
            clear: both;
            display: table;
        }
        .search-area {
            border-bottom: 1px solid $list-view-border-color;
            padding: 20px;
            position: relative;
            input {
                border-radius: $list-view-border-radius;
                -webkit-border-radius: $list-view-border-radius;
                width: 100%;
                max-width: 100%;
                border-color: darken($list-view-border-color, 5%) !important;
                color: $list-view-input-color;
                padding: 20px 10px !important;
                &::placeholder {
                    color: lighten($list-view-input-color, 60%) !important;
                }
                &:focus {
                    border-color: darken($list-view-border-color, 20%) !important;
                    &~button svg path:not([fill=none]) {
                        fill: $list-view-input-color;
                    }
                }
            }
	    button {
	        position: absolute;
                right: 23px;
                top: 29px;
                border: none;
                background: none !important;
                outline: none !important;
	        svg {
		    height: 22px;
		    path {
		        &:not([fill=none]) {
			    fill: lighten($list-view-input-color, 60%);
                            transition: 1s all;
		        }
		    }
	        }
	    }
        }

        .list-view-ul {
	    margin: 0;
            >li {
	        padding: 0;
	        margin: 0;
	        width: 100%;
	        list-style: none;
	        >.list-item-outer {
		    background: transparent;
		    text-align: left;
		    // cursor: pointer;
		    outline: none;
		    width: 100%;
		    line-height: 1.5em;
		    background: transparent;
		    color: inherit;
		    font: inherit;
		    -webkit-font-smoothing: inherit;
		    -moz-osx-font-smoothing: inherit;
		    -webkit-appearance: none;
                    .left-wrap {
	                display: flex;
	            }
	            .list-item-prepend {
	                display: flex;
	                align-items: center;
	                margin-right: 15px;
	            }
	            .list-item-append {
	                display: flex;
	                align-items: center;
	                margin-left: 15px;
	            }
	        }
	        &:last-of-type {
		    border-bottom: none;
	        }
	        .list-item-link {
		    display: block;
		    * {
		        pointer-events: none;
		    }
	        }
                .avatar {
	            border-radius: 50%;
	            -webkit-border-radius: 50%;
                }

	        .list-item-title {
		    text-overflow: ellipsis;
		    width: 100%;
		    overflow: hidden;
		    font-size: 1.2em;
                    &button {
                        border: none;
		        margin: 0;
		        padding: 0;
		        width: auto;
		        overflow: visible;
		        background: transparent;
		        color: inherit;
		        font: inherit;
		        line-height: normal;
		        -webkit-font-smoothing: inherit;
		        -moz-osx-font-smoothing: inherit;
		        -webkit-appearance: none;
		        cursor: pointer;
		        outline: none;
                    }
	        }
	        .list-item-overtitle {
		    font-size: 0.8em;
		    color: #000;
		    text-decoration: none;
		    opacity: 0.5;
	        }
	        .list-item-undertitle {
		    font-size: 0.8em;
		    color: #000;
		    text-decoration: none;
		    opacity: 0.5;
	        }
	        .dropdown {
		    .dropdown-item {
		        cursor: pointer;
		        opacity: 0.5;
		        outline: none;
		        padding: 10px 0 10px 10px;
		        text-align: left;
		        border: none;
		        transition: 0.3s opacity;
		        opacity: 1;
		        svg {
			    height: 27px;
		        }
		    }
		    .button {
		        svg {
			    width: 27px;
		        }
		    }
	        }
	    }
	    .list-item-outer {
	        width: 100%;
	        display: flex;
	        justify-content: space-between;
	        border-top: none;
	        border-left: none;
	        border-right: none;
	        border-bottom: 1px solid #ededed;
	        align-items: center;
	        padding: 10px 20px !important;
	    }
        }
    }
    .list-placeholder {
	text-align: center;
	position: relative;
	h1 {
	    margin: 50px;
	    font-size: 1.5em;
	}
	svg {
	    max-width: 600px;
	    margin: 40px 20px 80px 20px;
	}
    }
    &.borderless {
        .list-body {
            box-shadow: none;
            -webkit-box-shadow: none;
        }
        .list-view-ul {
	    border: none;
	    .list-item-outer {
	        border: none;
	    }
        }
    }
    &.compact {
        font-size: 0.8em;
        ul {
	    .list-item-outer {
	        padding-bottom: 1.5em !important;
	        .dropdown-trigger {
		    .button {
		        svg {
			    width: 22px;
		        }
		    }
	        }
	    }
        }
        .avatar {
	    width: 50px;
        }
    }
    &.gallery {
        >ul {
	    display: flex;
	    flex-wrap: wrap;
	    border: none;
	    padding: 20px;
	    >li {
	        border: none;
	        padding: 20px 30px;
	        min-width: 300px;
	        width: 33%;
	        max-width: 500px;
	        >.list-item-outer {
		    display: block;
		    text-align: center;
		    border: 1px solid #ededed;
		    border-radius: 10px;
		    -webkit-border-radius: 10px;
		    position: relative;
		    padding: 30px 20px !important;
	        }
	        .dropdown {
		    position: absolute;
		    right: 0;
		    top: 0;
		    z-index: 3;
	        }
	        .dropdown.dropdown-trigger {
		    .button {
		        svg {
			    width: 20px;
			    margin-right: 10px;
		        }
		    }
	        }
	    }
        }
        .left-wrap {
	    display: block;
        }
        .list-item-prepend {
	    margin: 0;
	    display: block;
        }
        .list-item-append {
	    margin: 0;
	    display: block;
        }
        .avatar {
	    border-radius: 50%;
	    margin: 0 0 20px 0;
        }
        .list-head-right {
	    margin-bottom: -30px;
        }
    }
    &.no_navbar {
        .list-head {
	    display: block;
        }
        .list-head-right {
	    justify-content: inherit;
	    flex-direction: row;
	    align-items: center;
	    margin-top: 20px;
	    margin-bottom: 10px;
        }
        .list-actions {
	    >* {
	        &:first-child {
		    margin-left: 0;
		    margin-right: 0;
	        }
	    }
        }
    }
    &.feed {
        >ul {
	    padding-left: 30px;
	    >li {
	        padding-left: 10px;
	        position: relative;
	        border-left: 2px solid #ededed;
	        &:after {
		    content: "";
		    display: block;
		    position: absolute;
		    top: 43%;
		    left: -8px;
		    width: 14px;
		    height: 14px;
		    border-radius: 100px;
		    background: #fff;
		    border: 2px solid #ededed;
	        }
	    }
        }
        &.compact {
            >ul {
	        padding-left: 25px;
	        >li {
	            padding-left: 5px;
	            &:after {
		        top: 33%;
	            }
	        }
            }
        }
    }

}
@media (max-width: 800px) {
    .list_view {
	.list-head {
	    display: block !important;
	    margin-bottom: 15px;
	    .list-actions {
		>.list-action-button {
		    &:first-of-type {
			margin-left: 0;
		    }
		    margin-bottom: 10px;
		}
	    }
	}
	.list-head-right {
	    margin-top: 10px !important;
	    display: block !important;
	}
    }
    .list_view.gallery {
	>ul {
	    margin: 0 !important;
	    >li {
		padding-left: 0 !important;
	    }
	}
    }
}
